"use client";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchProducts, selectProductsState } from "../../../../store/features/couter/fechDataSlice.js";

export default function Page() {
    const dispatch = useDispatch();
    const { products, status, error } = useSelector(selectProductsState);
  React.useEffect(() => {
      if (status === "idle") {
        dispatch(fetchProducts());
      }
      console.log(products);
    }, [status, dispatch]);
  
  return (
    <div className=" w-screen h-screen mt-[20px]">
      <div className=" w-10 h-10 bg-black"></div>
      <div>
        <div>Giới thiệu về sản phẩm</div>
        <div>
          {status === "succeeded" && (
            <div className="p-4 text-sm text-gray-500">{products.description}</div>
          )}
        </div>
      </div>
    </div>
  );
}
